body.modal-open {
	padding-right: 0px !important;
}

.loader {
  background-color: #fefefa;
}

.maintenancePage {
	background-color: #41999e;
	color: #ffffc7;
	padding: 0 5px;
	min-height: 100vh;
	position: relative;
	overflow-y: hidden;
}

.maintenancePage div {
	text-align: center;
	font-family: Impact !important;
}

.maintenancePage div a {
	color: #ffff00;
	text-decoration: underline;
}

.maintenancePage div h1 {
	font-weight: 700;
	font-size: 60px;
}

.maintenancePage div h2 {
	font-weight: 800;
	font-size: 22px;
}

.popover, .popover-body {
	pointer-events: none;
}

b {
	font-weight: 500;
}

strong {
	font-weight: 800;
}

